import React from 'react';
import {
    NoMatchDiv,
    NoMatchWrapper,
    NoMatchContent,
    NoFoundImage,
    NoFoundText
} from './style';
import {Link} from "react-router-dom";
import NoFound from "../../statics/picture/404_page_not_found.png";

function HospitalIntro() {
    return (
        <NoMatchWrapper>
            <NoMatchDiv>
                <NoMatchContent>
                    <NoFoundImage>
                        <img src={NoFound} alt="No Found"/>
                    </NoFoundImage>
                    <NoFoundText>
                        <h1></h1>
                        <p><Link to="/"></Link></p>
                    </NoFoundText>
                </NoMatchContent>
            </NoMatchDiv>
        </NoMatchWrapper>
    );
}

export default HospitalIntro;